import {
  Table,
  TableBody,
  TableExpandHeader,
  TableHead,
  TableHeader,
  TableRow,
  TableSelectAll,
} from "@carbon/react";
import React from "react";
import { EmptyTable } from "./Utils";
import RowFragment from "./RowFragment";

export default function Body({
  children,
  headers,
  rows,
  getTableProps,
  getSelectionProps,
  getHeaderProps,
  getRowProps,
}: any) {
  return (
    <Table {...getTableProps()}>
      <TableHead>
        <TableRow>
          <TableExpandHeader />
          <TableSelectAll {...getSelectionProps()} />
          {headers.map((header: any) => (
            <React.Fragment key={header.id}>
              <TableHeader {...getHeaderProps({ header, isSortable: true })}>
                {header.header}
              </TableHeader>
            </React.Fragment>
          ))}
        </TableRow>
      </TableHead>
      <TableBody>
        {rows.length == 0 && <EmptyTable />}
        {rows.map((row: any) => (
          <RowFragment
            key={row.id}
            row={row}
            getRowProps={getRowProps}
            getSelectionProps={getSelectionProps}
            headersLength={headers.length}
          />
        ))}
      </TableBody>
    </Table>
  );
}
