import { connect } from "react-redux";
import { AuthState } from "@/src/redux/auth.slice";
import { fetchArtifacts } from "@/src/redux/dashboard.slice";
import { DataTable, TableContainer } from "@carbon/react";
import { useEffect, useState } from "react";
import React from "react";

import CustomFooter from "./Footer";
import CustomHeader from "./Header";
import CustomBody from "./Body";

import { getTableHeaders } from "./Utils";

function DashboardForm({ authState, artifactList, fetchArtifacts }: any) {
  const headers = getTableHeaders();

  const [rows, setRows]: any = useState([]);
  console.log(artifactList);
  fetchArtifacts();

  useEffect(() => {
    const trows: any = [
      {
        name: "Imperial Bird",
        uploaded_at: "16:48 2021-05-01",
        published: true,
        cdn: false,
        marketplace: false,
      },
    ];
    artifactList.forEach((file: any, i: any) => {
      trows.push({
        id: file.id,
        name: file.binary.name,
        size: file.binary.size,
        status: file.status,
      });
    });
    setRows(trows);
  }, [artifactList]);

  if (authState === AuthState.SIGNED_IN) {
    return (
      <>
        <DataTable headers={headers} rows={rows}>
          {({
            rows,
            headers,
            getTableProps,
            getHeaderProps,
            getRowProps,
            getSelectionProps,
            getBatchActionProps,
            onInputChange,
            selectedRows,
          }: any) => {
            const batchActionProps = getBatchActionProps();
            return (
              <TableContainer title="" description="">
                <CustomHeader
                  batchActionProps={batchActionProps}
                  selectedRows={selectedRows}
                  onInputChange={onInputChange}
                />
                <CustomBody
                  headers={headers}
                  rows={rows}
                  getTableProps={getTableProps}
                  getSelectionProps={getSelectionProps}
                  getHeaderProps={getHeaderProps}
                  getRowProps={getRowProps}
                />
                <CustomFooter selectedRows={selectedRows} />
              </TableContainer>
            );
          }}
        </DataTable>
      </>
    );
  } else {
    return <>You need to be logged in</>;
  }
}

const mapStateToProps = (state: any) => ({
  authState: state.auth.authState,
  artifactList: state.dashboard.artifactList,
});

const mapDispatchToProps = (dispatch: any) => ({
  fetchArtifacts: () => dispatch(fetchArtifacts()),
});

export default connect(mapStateToProps, mapDispatchToProps)(DashboardForm);
