import {
  TableCell,
  //@ts-ignore
  InlineLoading,
  TableExpandRow,
  TableExpandedRow,
  TableSelectRow,
} from "@carbon/react";
import React from "react";
import { FileStatus } from "@/src/redux/upload.slice";
import { connect } from "react-redux";

function RowFragment({
  children,
  row,
  getRowProps,
  getSelectionProps,
  headersLength,
  uploadList,
}: any) {
  return (
    <React.Fragment key={row.id}>
      <TableExpandRow {...getRowProps({ row })}>
        <TableSelectRow {...getSelectionProps({ row })} />
        {row.cells.map((cell: any) => {
          console.log(cell);
          if (
            cell.info.header === "published" ||
            cell.info.header === "cdn" ||
            cell.info.header === "marketplace"
          ) {
            return (
              <TableCell key={cell.id}>{cell.value ? "Yes" : "No"}</TableCell>
            );
          } else {
            return <TableCell key={cell.id}>{cell.value}</TableCell>;
          }
        })}
      </TableExpandRow>
      <TableExpandedRow
        colSpan={headersLength + 2}
        className="demo-expanded-td"
      >
        {(() => {
          return (
            <>
              <h6>Uploaded Artifact details</h6>
              <div></div>
              <div>
                <strong>Link: </strong> <span></span>
              </div>
            </>
          );
        })()}
      </TableExpandedRow>
    </React.Fragment>
  );
}

const mapStateToProps = (state: any) => ({
  artifactList: state.dashboard.artifactList,
});

const mapDispatchToProps = (dispatch: any) => ({});

export default connect(mapStateToProps, mapDispatchToProps)(RowFragment);
