import {
  TableBatchAction,
  TableBatchActions,
  TableToolbar,
  TableToolbarContent,
  TableToolbarSearch,
} from "@carbon/react";
//@ts-ignore
import { TrashCan as TrashCanIcon } from "@carbon/icons-react";
import { connect } from "react-redux";

function Header({
  children,
  batchActionProps,
  selectedRows,
  onInputChange,
}: any) {
  return (
    <TableToolbar>
      <TableBatchActions {...batchActionProps}>
        <TableBatchAction
          tabIndex={batchActionProps.shouldShowBatchActions ? 0 : -1}
          renderIcon={TrashCanIcon}
          onClick={(_: any) => {}}
        >
          Delete
        </TableBatchAction>
      </TableBatchActions>
      <TableToolbarContent>
        <TableToolbarSearch onChange={onInputChange} />
      </TableToolbarContent>
    </TableToolbar>
  );
}

const mapStateToProps = (state: any) => ({
  artifactList: state.dashboard.artifactList,
});

const mapDispatchToProps = (dispatch: any) => ({});

export default connect(mapStateToProps, mapDispatchToProps)(Header);
